/* Estilos encapsulados para evitar interferencias */
.register-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-size: 200% 200%;
    animation: gradientAnimation 6s linear infinite; /* Aplica la animación */
    max-width: 600px;
    min-width: 400px;
    margin: auto
  }
  
  @keyframes gradientAnimation {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
  
  .register-form {
    width: 100%;
    max-width: 400px;
    padding: 20px;
    background-color: var(--blanco); /* Fondo blanco para contraste */
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .register-title {
    font-size: 24px;
    font-weight: bold;
    color: var(--color-primario); /* Color primario */
    text-align: center;
    margin-bottom: 20px;
  }
  
  .register-input-group {
    margin-bottom: 5px;
  }
  
  .register-label {
    display: block;
    font-size: 14px;
    color: var(--color-secundario); /* Color secundario */
    margin-bottom: 5px;
  }
  
  .register-input {
    width: 100%;
    padding: 10px;
    font-size: 14px;
    border: 1px solid var(--color-terciario); /* Color terciario */
    border-radius: 4px;
    box-sizing: border-box;
  }
  
  .register-input:focus {
    border-color: var(--color-primario); /* Color primario */
    outline: none;
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
  }
  
  .register-password-container {
    position: relative;
    display: flex;
    align-items: center;
  }
  
  .register-toggle-password {
    margin-left: 10px;
    font-size: 12px;
    color: var(--color-primario); /* Color primario */
    background: none;
    border: none;
    cursor: pointer;
  }
  
  .register-toggle-password:hover {
    text-decoration: underline;
    color: var(--color-secundario); /* Color secundario */
  }
  
  .register-submit-button {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    color: #ffffff;
    background-color: var(--color-primario); /* Color primario */
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .register-submit-button:hover {
    background-color: var(--color-secundario); /* Color secundario */
  }
  
  .register-login-link {
    font-size: 14px;
    text-align: center;
    margin-top: 10px;
  }
  
  .register-login-link a {
    color: var(--color-primario); /* Color primario */
  text-decoration: none;

  }
  
  .register-login-link a:hover {
    text-decoration: underline;
    color: var(--color-secundario); /* Color secundario */
  }
  
  .register-password-container {
    display: flex;
    align-items: center;
  }
  
  .register-toggle-password {
    margin-left: 10px;
    font-size: 12px;
    color: var(--color-primario); /* Color primario */
    background: none;
    border: none;
    cursor: pointer;
  }
  
  .register-toggle-password:hover {
    text-decoration: underline;
    color: var(--color-secundario); /* Color secundario */
  }
  
  .no-navbar {
    padding-top: 0;
  }
  .register-login-link-text {
    color: var(--color-primario); /* Color primario */
    text-decoration: none;
    cursor: pointer;
  }
  
  .register-login-link-text:hover {
    text-decoration: underline;
    color: var(--color-secundario); /* Color secundario */
  }

.password-container-login {
  position: relative;
}

.password-toggle-icon-login {
  position: absolute;
  right: 10px;
  top: 55%; /* Adjusted position */
  transform: translateY(-50%);
  cursor: pointer;
}


